<style lang="scss">@import "./css/style.scss";</style>
<template>
    <div id="index">
        <ToolBar>
           <div></div>
            <div>
                <el-input
                        placeholder="请输入手机号或姓名"
                        size="small"
                        style="width: 220px"
                        v-model="searchParams.postTitle"
                        clearable>
                </el-input>
                <el-select v-model="searchParams.postType" size="small" clearable placeholder="认证类型"
                           style="width: 140px">
                    <el-option
                            v-for="(v,k) in $Cfg.postType"
                            :key="k"
                            :label="v"
                            :value="k">
                    </el-option>
                </el-select>

                <el-select v-model="searchParams.postStatus" size="small" clearable placeholder="证书名称"
                           style="width: 120px">
                    <el-option
                            v-for="(v,k) in $Cfg.postStatus"
                            :key="k"
                            :label="v"
                            :value="k">
                    </el-option>
                </el-select>
                <el-select v-model="searchParams.postStatus" size="small" clearable placeholder="请选择状态"
                           style="width: 120px">
                    <el-option
                        v-for="(v,k) in $Cfg.postStatus"
                        :key="k"
                        :label="v"
                        :value="k">
                    </el-option>
                </el-select>
                <el-button type="success" size="small" @click="refresh = !refresh">查询</el-button>
                <el-button type="warning" size="small">重置</el-button>
            </div>
        </ToolBar>
        <el-table
                :data="tableData"
                border
                style="width: 100%">
            <el-table-column
                    prop="date"
                    label="id"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="name"
                    label="姓名"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="address"
                    label="注册手机号">
            </el-table-column>
            <el-table-column
                prop="address"
                label="证书名称">
            </el-table-column>
            <el-table-column
                prop="address"
                label="证书编号">
            </el-table-column>
            <el-table-column
                prop="address"
                label="认证日期">
            </el-table-column>
            <el-table-column
                prop="address"
                label="审核时间">
            </el-table-column>
            <el-table-column
                prop="address"
                label="状态">
            </el-table-column>
            <el-table-column
                    label="操作"
                    width="78">
                <template slot-scope="scope">
                    <el-button v-if="scope.row.isopen" @click="checkAuthen(scope.row)" type="info" size="small">查看</el-button>
                    <el-button v-else @click="auditeAuthen(scope.row)" type="primary" size="small"
                               >审核</el-button>
                </template>
            </el-table-column>
        </el-table>
        <Pagination
            :params="searchParams"
            :requestFunc="requestFunc"
            @returnData="returnData"
        />
    </div>
</template>

<script>
    import ToolBar from '@/components/ToolBar.vue';
    import HelpHint from '@/components/HelpHint.vue';
    import Pagination from '@/components/Pagination.vue';
    import paginationMixin from './mixin/page.js'

    export default {
        mixins:[paginationMixin],
        data() {
            return {
                tableData: [{
                    date: '2016-05-03',
                    name: 'Lorem ipsum dolor sit amet,',
                    isopen: false,
                    address: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Asperiores fugit in quae vero. Adipisci blanditiis dignissimos eum facere laudantium quasi ratione repellat vitae! Alias consequatur dolores enim neque similique unde.'
                }, {
                    date: '2016-05-02',
                    name: 'Lorem ipsum dolor sit amet,',
                    isopen: true,
                    address: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Asperiores fugit in quae vero. Adipisci blanditiis dignissimos eum facere laudantium quasi ratione repellat vitae! Alias consequatur dolores enim neque similique unde.'
                }]
            }
        },
        methods: {
            handleClick(row) {
                this.$alert(row, '标题名称', {
                    confirmButtonText: '确定',
                    callback: action => {}
                });
            },
            auditeAuthen() {
                this.$router.push({ path: '/authentication/index/audite'})
            },
            checkAuthen() {
                this.$router.push({ path: '/authentication/index/check'})
            }
        },
        created() {

        },
        components: {
            ToolBar, HelpHint, Pagination
        }
    }
</script>
